.product-card-dropdown {
  &__item {
    border-bottom: solid 1px $gray-border;
    &:last-child {
      border-bottom: none;
      @include screen(tablet) {
        border-bottom: solid 1px $gray-border;
      }
    }
  }
  &__title {
    padding: 14px 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    @include screen(tablet) {
      padding: 20px;
    }
    @include screen(laptop) {
      padding: 35px 30px;
      &:hover {
        .product-card-dropdown__arrow {
          border-radius: 50%;
          transition: background-color $transition-default;
          path {
            transition: fill $transition-default;
          }
        }
        .product-card-dropdown__arrow {
          background-color: black;
          path {
            fill: $white;
          }
        }
      }
    }
    span {
      font-family: 'Tenor Sans';
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
  &__arrow {
    transition: transform $transition-default;
    &.active {
      transform: rotate(180deg);
    }
  }
  &__description {
    padding: 0 10px;
    opacity: 0;
    visibility: hidden;
    position: absolute;
    z-index: -1;
    transition: opacity $transition-default;
    &.active {
      position: relative;
      opacity: 1;
      visibility: visible;
      z-index: 1;
      transition: opacity $transition-default;
    }
    @include screen(tablet) {
      padding: 0 20px;
    }
    @include screen(laptop) {
      padding: 0 30px;
    }
  }
  &__information {
    display: flex;
    flex-direction: column;
    gap: 5px;
    p {
      font-size: 14px;
      line-height: 18px;
      span {
        font-weight: 500;
      }
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
    @include screen(laptop) {
      gap: 10px;
    }
  }
  &__text {
    display: flex;
    flex-direction: column;
    padding-bottom: 20px;
    max-height: 500px;
    overflow-y: auto;
    p, ul {
      opacity: 0.8;
    }
    p, ul, h5, span {
      margin-top: 20px;
      font-size: 14px;
      line-height: 18px;
    }
    span {
      color: $gray-600;
    }
    ul {
      list-style: none;
      li {
        text-indent: 18px;
        position: relative;
        &:before {
          content:  "—";
          position: absolute;
          left: -18px;
        }
      }
    }
    h5 {
      font-size: 14px;
      font-weight: 500;
      line-height: 18px;
    }
    h5 + p,
    h5 + span,
    h5 + ul {
      margin-top: 8px;
    }
    @include screen(laptop) {
      padding-bottom: 30px;
      p, ul, h5, span {
        margin-top: 30px;
        font-size: 16px;
        line-height: 21px;
      }
    }
    &-noinfo {
      p, ul, h5, span {
        margin-top: 0;
      }
    }
  }
  &__download {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 20px;
    @include screen(laptop) {
      margin-top: 32px;
    }
    span {
      margin-top: 0;
      font-weight: 500;
      color: $black-400;
      opacity: 0.8;
    }
  }
}