.news-post {
  padding-top: 20px;
  padding-bottom: 60px;
  @include screen(tablet) {
    padding-top: 32px;
  }
  @include screen(laptop) {
    padding-bottom: 100px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 20px;
    @include screen(tablet) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      gap: 30px 10px;
    }
  }
  &__img {
    width: 100%;
    img {
      object-fit: cover;
      aspect-ratio: 1 / 1;
    }
  }
  &__title {
    margin-top: 15px;
    font-family: 'Tenor Sans';
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    max-width: 220px;
    overflow: hidden;
    @include screen(laptop) {
      margin-top: 20px;
      font-size: 16px;
      line-height: 21px;
      max-width: 290px;
    }
  }
  &__text {
    margin-top: 10px;
    color: $gray-700;
    opacity: 0.6;
    font-size: 14px;
    line-height: 18px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
  }
}