.product-card-info {
  &.drawed {
    .product-card-info {
      &__wrapper {
        &:before {
          width: 100%;
        }
        &:after {
          height: 100%;
        }
      }
      &__inner {
        &:before {
          height: 100%;
        }
        &:after {
          width: 100%;
        }
      }
      &__content {
        &:before {
          height: 100%;
        }
      }
    }
  }
  &__inner {
    position: relative;
    &:before {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      height: 0;
      width: 1px;
      transition: $transition-slow;
      background-color: $gray-border;
    }
    &:after {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      height: 1px;
      width: 0;
      transition: $transition-slow;
      background-color: $gray-border;
    }
  }
  &__wrapper {
    position: relative;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      transition: $transition-slow;
      top: 0;
      width: 0;
      height: 1px;
      background-color: $gray-border;
    }
    &:after {
      content: '';
      transition: $transition-slow;
      position: absolute;
      left: 0;
      top: 0;
      height: 0;
      width: 1px;
      background-color: $gray-border;
    }
    @include screen(tablet) {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(laptop) {
      grid-template-columns: 685px 1fr;

    }
  }
  &__slider {
    padding-top: 40px;
    position: relative;
    @include screen(tablet) {
      padding-top: 74px;
    }
    @include screen(laptop) {
      padding-top: 80px;
      padding-bottom: 46px;
    }
  }
  &__content {
    padding-top: 27px;
    @include screen(tablet) {
      padding-top: 25px;
      position: relative;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        height: 0;
        transition: $transition-slow;
        width: 1px;
        background-color: $gray-border;
      }
    }
    @include screen(laptop) {
      padding-top: 35px;
    }
  }
  &__information {
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 32px;
    border-bottom: 1px solid rgba($gray, 0.5);
    @include screen(tablet) {
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 20px;

    }
  }
  &__brand {
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.02em;
    opacity: 0.6;
    text-transform: uppercase;
    @include screen(tablet) {
      font-size: 14px;
      line-height: 18px;
    }
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__name {
    margin-top: 5px;
    text-transform: uppercase;
    font-family: 'Tenor Sans';
    font-size: 20px;
    line-height: 26px;
    letter-spacing: 0.02em;
    @include screen(tablet) {
      margin-top: 10px;
      font-size: 24px;
      line-height: 32px;
    }
    @include screen(laptop) {
      font-size: 28px;
      line-height: 36px;
    }
  }
  &__amount {
    margin-top: 5px;
    font-size: 14px;
    line-height: 18px;
    opacity: 0.6;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 19px;
    }
  }
  &__description {
    margin-top: 15px;
    font-size: 14px;
    line-height: 18px;
    opacity: 0.8;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__buttons {
    margin-top: 32px;
    display: flex;
    justify-content: space-between;
    gap: 10px;
    @include screen(tablet) {
      margin-top: 16px;
    }
    @include screen(laptop) {
      margin-top: 26px;
      justify-content: flex-start;
    }
    a {
      width: 100%;
      @include screen(laptop) {
        width: auto;
      }
    }
    .button {
      width: 100%;
      @include screen(laptop) {
        width: 165px;
      }
    }
  }

  .swiper-main {
    .swiper-slide {
      display: flex;
      justify-content: center;
    }
    .swiper-wrapper {
      height: 393px;
      @include screen(tablet) {
        height: 770px;
      }
    }
    img {
      max-height: 393px;
      object-fit: contain;
      width: auto;
      @include screen(tablet) {
        max-height: 770px;
      }
    }
  }

  .swiper-products {
    margin: 0 55px;
    img {
      max-height: 82px;
      width: auto;
      object-fit: contain;
    }
    @include screen(laptop) {
      margin: 0 150px;
      img {
        max-height: 140px;
      }
    }
    .swiper-slide {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  .swiper-button {
    &-prev,
    &-next {
      display: flex;
      margin: 0;
      width: 35px;
      height: 35px;
      border-radius: 50%;
      border: 1px solid $black-400;
      background-color: $white;
      align-items: center;
      justify-content: center;
      top: auto;
      bottom: 25px;
      left: auto;
      right: auto;
      @include screen(tablet) {
        bottom: auto;
        top: 866px;
      }
      @include screen(laptop) {
        width: 60px;
        height: 60px;
        top: 890px;
        transition: background-color $transition-default;
        &:hover {
          background-color: $black-hover;
        }
      }
      &:after,
      &:before {
        display: none;
      }
      svg {
        width: 18px;
        @include screen(laptop) {
          width: 25px;
        }
      }
    }
    &-prev {
      left: 10px;
      @include screen(tablet) {
        left: 22px;
      }
      @include screen(laptop) {
        left: 70px;
      }
    }
    &-next {
      right: 10px;
      @include screen(tablet) {
        right: 22px;
      }
      @include screen(laptop) {
        right: 70px;
      }
      svg {
        transform: rotate(180deg);
      }
    }
  }
  .swiper-button-prev.swiper-button-disabled, .swiper-button-next.swiper-button-disabled {
    opacity: 1;
  } 
}