.breadcrumbs {
  position: relative;
  z-index: 4;
  padding: 20px 0;
  @include screen(laptop) {
    padding-top: 40px;
    padding-bottom: 30px;

  }
  &__list {
    gap: 5px;
    li {
      display: inline;
    }
    li,
    a, 
    span, 
    p {
      font-size: 14px;
      line-height: 18px;
      color: $gray-500;
    }
    span {
      margin: 0 4px;
    }
  }
}