.not-found {
  height: 100%;
  &.drawed {
    h1 {
      span {
        transform: rotate(16.55deg);
      }
    }
  }
  &__wrapper {
    height: calc(100vh - 73px);
    display: flex;
    align-items: center;
    justify-content: center;
    @include screen(tablet) {
      padding-bottom: 60px;
      align-items: flex-end;
    }
    @include screen(laptop) {
      padding-bottom: 0;
      align-items: center;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  h1 {
    font-family: 'Tenor Sans';
    font-size: 176px;
    line-height: 299px;
    letter-spacing: 0.02em;
    color: $black-hover;
    @include screen(tablet) {
      font-size: 318px;
      line-height: 414px;
    }
    span {
      display: inline-block;
      transition: transform $transition-default;
    }
  }
  h2 {
    font-family: 'Tenor Sans';
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.02em;
    text-align: center;
    @include screen(tablet) {
      font-size: 28px;
      line-height: 36px;
    }
  }
  p {
    margin-top: 9px;
    font-size: 9px;
    line-height: 12px;
    text-align: center;
    color: $gray-800;
    max-width: 140px;
    @include screen(tablet) {
      margin-top: 16px;
      font-size: 16px;
      line-height: 21px;
      max-width: 250px;
    }
  }
  &__button {
    margin-top: 17px;
    @include screen(tablet) {
      margin-top: 32px;
    }
  }
}