.news-filter {
  position: relative;
  z-index: 3;
  padding-top: 30px;
  @include screen(tablet) {
    padding-top: 60px;
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 30px;
    @include screen(tablet) {
      gap: 24px;
    }
    @include screen(laptop) {
      flex-direction: row;
      justify-content: space-between;
      position: relative;
      &:before,
      &:after {
        position: absolute;
        content: '';
        height: 1px;
        background-color: $gray;
        opacity: 0.5;
        left: 0;
        width: 100%;
      }
      &:before {
        top: 0;
      }
      &:after {
        bottom: 0;
      }
    }
  }
  &__items {
    padding: 15px 0;
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 10px;
    @include screen(tablet) {
      display: flex;
      padding: 20px 0;
    }
    @include screen(tablet) {
      padding: 32px 0;
    }
    &:before,
    &:after {
      position: absolute;
      content: '';
      left: -20px;
      width: calc(100% + 40px);
      height: 1px;
      background-color: $gray;
      opacity: 0.5;
      @include screen(tablet) {
        left: -0;
        width: 100%;
      }
      @include screen(laptop) {
        display: none;
      }
    }
    &:before {
      top: 0;
    }
    &:after {
      bottom: 0;
    }
    .button {
      &:hover {
        background-color: $black-400;
        span {
          color: $white;
        }
      }
    }
  }
  &__buttons {
    @include screen(laptop) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  &-dropdown {
    min-width: 160px; 
    max-width: max-content;
    border: 1px solid $black-400;
    position: relative;
    &__button {
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: space-between; 
      gap: 10px;
      padding: 8px 12px;
      &.active {
        svg {
          transform: rotate(180deg);
        }
      }
    }
    &__value {
      font-size: 14px;
      line-height: 18px;
    }
    input {
      position: absolute;
      width: 0;
      height: 0;
      opacity: 0;
      visibility: hidden;
    }
    &__arrow {
      transition: transform $transition-default;
    }
    &__items {
      background-color: $white;
      display: flex;
      flex-direction: column;
      gap: 14px;
      padding: 10px 19px 17px 12px;
      opacity: 0;
      width: calc(100% + 2px);
      left: -1px;
      z-index: -1;
      visibility: hidden;
      border: 1px solid $black-400;
      border-top: 0;
      position: absolute;
      transition: visibility $transition-default, opacity $transition-default, z-index $transition-default;
      &.active {
        opacity: 1;
        z-index: 3;
        visibility: visible;
      }
      span {
        display: inline-block;
        cursor: pointer;
        font-size: 14px;
        line-height: 18px;
      }
    }
  }
}