.brands-card {
  @include screen(laptop) {
    display: flex;
    gap: 30px;
  }
  &-reverse {
    @include screen(laptop) {
      display: flex;
      flex-direction: row-reverse;
    }
  }
  &__content {
    margin-top: 22px;
    @include screen(tablet) {
      margin-top: 24px;
      display: grid;
      grid-template-columns: 258px 1fr;
      column-gap: 33px;
    }
    @include screen(laptop) {
      display: flex;
      flex-direction: column;
      margin-top: 10px;
      justify-content: space-between;
    }
  }
  &__title {
    font-family: 'Tenor Sans';
    font-size: 16px;
    line-height: 21px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    @include screen(tablet) {
      grid-row: 1 / span 1;
    }
    @include screen(laptop) {
      font-size: 20px;
      line-height: 26px;
      order: 1;
    }
  }
  &__text {
    margin-top: 16px;
    font-size: 14px;
    line-height: 18px;
    @include screen(tablet) {
      grid-row: 2 / span 2;
    }
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
      order: 2;
      max-width: 380px;
    }
  }
  &__quality {
    margin-top: 34px;
    border: 1px solid $black-400;
    padding: 12px 18px;
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
    @include screen(tablet) {
      grid-row: 4 / span 2;
      margin-top: 16px;
      padding: 13px 7px;
    }
    @include screen(laptop) {
      order: 4;
      margin-top: 65px;
      width: max-content;
      padding: 20px 32px;
    }
    svg {
      width: 45px;
      height: 20px;
      @include screen(laptop) {
        width: 90px;
        height: 40px;
      }
    }
    span {
      font-size: 14px;
      line-height: 18px;
      opacity: 0.8;
      @include screen(tablet) {
        font-size: 12px;
        line-height: 15px;

      }
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
        max-width: 290px;
      }
    }
  }
  &__items {
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    row-gap: 16px;
    column-gap: 22px;
    @include screen(tablet) {
      margin-top: 16px;
      grid-column: 2 / span 1;
      grid-row: 2 / span 6;
    }
    @include screen(laptop) {
      margin-top: 42px;
      column-gap: 88px;
      order: 3;
    }
  }
  &__item {
    svg {
      width: 38px;
      height: 36px;
      @include screen(laptop) {
        width: 53px;
        height: 50px;
      }
    }
    span {
      display: inline-block;
      margin-top: 10px;
      font-size: 14px;
      line-height: 18px;
      opacity: 0.8;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
  &__button {
    margin-top: 34px;
    display: inline-block;
    @include screen(tablet) {
      grid-row: 6 / span 2;
      margin-top: 24px;
    }
    @include screen(laptop) {
      order: 5;
      margin-top: 40px;
    }
  }
  &__slider {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    max-width: 685px;
    img {
      width: 100%;
    }
    .swiper {
      width: 100%;
    }
  }
  &__thumbs {
    .swiper-slide {
      cursor: pointer;
    }
  }
}