.popup-filter {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  background-color: $white;
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  @include screen(laptop) {
    position: absolute;
    width: 710px;
    right: 0;
    left: auto;
    top: 182px;
    height: calc(100% - 182px);
    box-shadow: 0px 39px 85px 0px #E2E2E21A;
    transition: opacity $transition-default;
    overflow: hidden;
  }
  &.active {
    z-index: 2901;
    opacity: 1;
    visibility: visible;
    @include screen(laptop) {
      z-index: 2500;
      transition: opacity $transition-default;
    }
  }
  &__wrapper {
    width: 100%;
    height: 100%;
    padding: 30px 20px;
  }
  &__body {
    overflow-y: auto;
    
    height: calc(100% - 123px);
    &::-webkit-scrollbar {
      width: 10px;
      background-color: $gray-100;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $black-400;
    }
  }
  &__top {
    padding-bottom: 20px;
    border-bottom: 1px solid $gray-400;
  }
  &__header {
    display: flex;
    justify-content: space-between;
  }
  &__switchers {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  &__switcher {
    display: flex;
    justify-content: space-between;
  }
  &-switcher {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 22px;
    input {
      opacity: 0;
      width: 0;
      height: 0;
      &:checked + .popup-filter-switcher__toggle {
        &:before {
          -webkit-transform: translateX(18px);
          -ms-transform: translateX(18px);
          transform: translateX(18px);
          background-color: $black-400;
        }
      } 
    }
    &__toggle {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: $gray-200;
      -webkit-transition: $transition-default;
      transition: $transition-default;
      border-radius: 100px;
      &:before {
        position: absolute;
        border-radius: 50%;
        content: "";
        height: 18px;
        width: 18px;
        left: 2px;
        bottom: 2px;
        border: 1px solid $black-400;
        -webkit-transition: $transition-default;
        transition: $transition-default;
      }
    }
  }
  &__type {
    margin-top: 20px;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin-top: 16px;
  }
  &__label {
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  &__checkbox {
    &-uppercase {
      text-transform: uppercase;
    }
  }
  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;
    & ~ .popup-filter__checkbox {
      position: relative;
      padding-left: 30px;
      &:before {
        position: absolute;
        content: '';
        display: inline-block;
        top: 50%;
        left: 0%;
        transform: translate(0, -50%);
        border: 1px solid $black-400;
        width: 20px;
        height: 20px;
      }
    }
    &:checked ~ .popup-filter__icon {
      opacity: 1;
      transition: opacity $transition-default;
    }
  }
  &__icon {
    width: 12px;
    height: 12px;
    border: 1px solid $black-400;
    border-radius: 50%;
    position: absolute;
    left: 4px;
    top: 6px;
    opacity: 0;
    transition: opacity $transition-default;
    &:before {
      position: absolute;
      content: '';
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: $black-400;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  &__buttons {
    display: flex;
    gap: 10px;
    margin-top: 24px;
  }
  .button-transparent {
    padding: 16px 20px;
    justify-content: flex-start;
    svg {
      display: block;
    }
  }
}
