.search-popup {
  position: absolute;
  top: 100%;
  right: 0;
  width: 335px;
  height: 332px;
  background-color: $white;
  overflow: hidden;

  z-index: -1;
  opacity: 0;
  visibility: hidden;
  transition: visibility $transition-default, opacity $transition-default, z-index $transition-default;
  &.active {
    z-index: 1;
    opacity: 1;
    visibility: visible;
  }
  &__wrapper {
    padding: 15px 20px;
  }
  &__items {
    display: flex;
    flex-direction: column;
    gap: 10px;
    overflow-y: auto;
    height: calc(332px - 30px);
    &::-webkit-scrollbar {
      width: 4px;
      background-color: $gray-100;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $black-400;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__img {
    border: 1px solid $gray-300;
    min-width: 43px;
    min-height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 16px;
    img {
      max-width: 12px;
      height: auto;
    }
  }
  &__brand {
    text-transform: uppercase;
    font-size: 10px;
    line-height: 13px;
    color: rgba($gray, 0.5);
  }
  &__name {
    margin-top: 5px;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
  }
  &__amount {
    margin-top: 10px;
    font-size: 10px;
    line-height: 12px;
    color: rgba($gray, 0.5);

  }
}