.brands {
  padding-bottom: 60px;
  @include screen(laptop) {
    padding-bottom: 100px;
  }
  &__container {
    margin-top: 16px;
    display: flex;
    flex-direction: column;
    gap: 60px;
  
    @include screen(tablet) {
      margin-top: 20px;
    }
    @include screen(laptop) {
      margin-top: 22px;
      gap: 80px;
    }
  }
}