.brand-popup {
  position: fixed;
  top: 60px;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  background-color: $white;
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  transition: opacity $transition-default, z-index $transition-default, visibility $transition-default;
  @include screen(laptop) {
    top: 73px;
  }
  &.active {
    z-index: 2901;
    opacity: 1;
    visibility: visible;
  }
  &__main {
    height: calc(100% - 84px);
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 10px;
      background-color: $gray-100;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $black-400;
    }
  }
  .breadcrumbs {
    &__list {
      a,
      span,
      p {
        color: $gray-500;
      }
    }
  }
  &__wrapper {
    width: 100%;
    height: 100%;
    padding: 0 0 50px 0;
  }
  &__container {
    display: flex;
    align-items: center;
    margin-top: 102px;
    position: relative;
    @include screen(tablet) {
      margin-top: 0;
      padding: 0 30px;
    }
    @include screen(laptop) {
      height: calc(100vh - 222px);
    }
  }
  &__inner {
    cursor: none;
    position: relative;
    @include screen(tablet) {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
      column-gap: 246px;
      row-gap: 60px;
    }
    @include screen(laptop) {
      grid-template-columns: repeat(5, minmax(0, 1fr));
      gap: 40px;
      align-items: center;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    gap: 50px;
    align-items: center;
    cursor: none;
    transform: none;
    @include screen(tablet) {
      gap: 40px;
    }
    @include screen(laptop) {
      gap: 30px;
    }
    span {
      text-transform: uppercase;
      font-family: 'Tenor Sans';
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.02em;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
  &__button {
    position: relative;
    margin-top: 74px;
    display: inline-flex;
    gap: 2px;
    align-items: center;
    span {
      font-size: 16px;
      line-height: 21px;
    }
    @include screen(tablet) {
      margin-top: 80px;
    }
    @include screen(laptop) {
      position: absolute;
      left: 20px;
      position: absolute;
      bottom: 110px;
      left: 20px;
      margin-top: 0;
      bottom: 110px;
    }
  }
  &__cursor {
    position: fixed;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.3s ease;
    width: 172px;
    height: 172px;
    border-radius: 50%;
    border: 1px solid $black-400;
    background-color: rgba($white, 0.5);
    display: flex;
    z-index: 100;
    transform: translate(-50%, -50%);
    top: 0;
    left: 0;
    align-items: center;
    justify-content: center;
    span {
      font-size: 16px;
      line-height: 21px;
    }
  }
  .swiper {
    &-wrapper {
      @include screen(tablet) {
        display: grid;
      }
    }
  }
  &__navigation {
    .swiper-button-next {
      display: flex;
      position: absolute;
      top: -70px;
      right: 0;
      margin: 0;
      width: max-content;
      justify-content: flex-end;
      gap: 10px;
      span {
        color: $black-400;
      }
      svg {
        width: 19px;
      }
      &:after {
        display: none;
      }
    }
  }
}