.loading {
  &__button {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    @include screen(tablet) {
      margin-top: 40px;
    }
    @include screen(laptop) {
      margin-top: 60px;
    }
  }
  &__pagination {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 16px;
    @include screen(tablet) {
      margin-top: 44px;
    }
    @include screen(laptop) {
      margin-top: 72px;
    }
  }
  &__number {
    font-size: 14px;
    line-height: 18px;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
    &.active {
      font-size: 16px;
      font-weight: 600;
      line-height: 21px;
      @include screen(laptop) {
        font-weight: 700;
      }
    }
  }
}