.brand-item {
  &.drawed {
    .brand-item {
      &__wrapper {
        &:before {
          width: calc(100% + 40px);
        }
      }
      &__block {
        @include screen(tablet) {
          &:before,
          &:after {
            height: 100%;
          }
        }
      }
    }
  }
  &__wrapper {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    @include screen(tablet) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
    &:before {
      position: absolute;
      content: '';
      left: -20px;
      transition: $transition-slow;
      bottom: 0;
      width: 0;
      height: 1px;
      background-color: $gray-400;
    }
  }
  &__block {
    padding: 43px 0 13px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: relative;
    @include screen(tablet) {
      padding: 57px 0;
      align-items: center;
    }
    @include screen(laptop) {
      padding: 66px 0;
    }
    &:before {
      @include screen(tablet) {
        position: absolute;
        content: '';
        top: 0;
        right: 0;
        width: 1px;
        transition: $transition-slow;
        height: 0;
        background-color: $gray-400;
      }
    }
    &:first-child {
      &:before {
        position: absolute;
        content: '';
        top: 0;
        right: 0;
        width: 1px;
        transition: $transition-slow;
        height: 100%;
        background-color: $gray-400;
        @include screen(laptop) {
          display: none;
        }
      }
      &:after {
        @include screen(laptop) {
          position: absolute;
          content: '';
          transition: $transition-slow;
          top: 0;
          right: 0;
          width: 1px;
          height: 0;
          background-color: $gray-400;
        }
      }
    }
    &:last-child {
      grid-column: 1 / span 2;
      @include screen(tablet) {
        grid-column: auto;
      }
      &:before {
        position: absolute;
        content: '';
        top: 0;
        left: -20px;
        width: calc(100% + 40px);
        transition: $transition-slow;
        height: 1px;
        background-color: $gray-400;
        @include screen(tablet) {
          display: none;
        }
      }

    }
  }
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    svg {
      width: 49px;
      height: 49px;
      @include screen(tablet) {
        width: 66px;
        height: 66px;
      }
    }
  }
  &__text {
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    margin-top: 12px;
    @include screen(tablet) {
      margin-top: 24px;
    }
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
    &-gray {
      margin-top: 5px;
      opacity: 0.6;
    }
  }
}