.header-desktop {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  z-index: -20;
  left: 0;
  overflow-y: hidden;
  width: 100%;
  top: 73px;
  height: calc(100% - 73px);
  transform: translateX(0);
  transition: 
    opacity $transition-default 350ms, 
    visibility $transition-default 350ms, 
    z-index $transition-default 350ms;
}