.product-card-using {
  margin-top: 60px;
  background-color: $black-400;
  &__wrapper {
    padding: 40px 0;
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 10px;
    @include screen(tablet) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      padding: 56px 0;
    }
    @include screen(laptop) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      padding: 60px 0;
    }
  }
  &__item {
    min-height: 360px;
    width: 100%;
    @include screen(tablet-2x) {
      min-height: 487px;
    }
    @include screen(laptop) {
      min-height: 452px;
    }
    @include screen(laptop) {
      min-height: 406px;
    }
    @include screen(desktop) {
      min-height: 460px;
    }
    span {
      display: inline-block;
      font-size: 14px;
      line-height: 18px;
      color: $white;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
    &-circle {
      min-height: 336px;
      padding: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      border: 1px solid $white;
      border-radius: 50%;
      margin-bottom: 10px;
      @include screen(tablet) {
        min-height: 360px;
      }
      @include screen(tablet-2x) {
        min-height: 487px;
      }
      @include screen(laptop) {
        min-height: 406px;
      }
      @include screen(desktop) {
        min-height: 460px;
        margin-bottom: 8px;
      }
      span {
        color: $gray-400;
      }
    }
    &-bg {
      border-radius: 30px;
      padding: 30px 23px;
      position: relative;
      background-color: $black-300;
      @include screen(tablet) {
        padding: 23px;
      }
      @include screen(laptop) {
        padding: 23px  33px;
      }
      svg {
        position: absolute;
        right: 23px;
        top: 23px;
        max-height: 147px;
        max-width: 148px;
        @include screen(tablet) {
          max-height: 175px;
          max-width: 174px;
        }
        @include screen(laptop) {
          max-height: 192px;
          max-width: 192px;
        }
      }
    }
    &-center {
      padding: 30px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      @include screen(tablet) {
        padding: 30px;
      }
      @include screen(laptop) {
        padding: 60px;
      }
    }
  }
  &__name {
    opacity: 0.5;
    position: absolute;
    @include screen(laptop) {
      top: 23px;
      left: 33px;
    }
  }
  &__number {
    color: $white;
    font-size: 37px;
    line-height: 100%;
    font-family: 'Tenor Sans';
    @include screen(tablet) {
      font-size: 47px;
    }
    @include screen(laptop) {
      font-size: 52px;
    }
  }
  &__product {
    font-family: 'Tenor Sans';
    margin-top: 26px;
    color: $white;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.02em;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 130%;
    }
  }
  &__text {
    margin-top: 10px;
    @include screen(laptop) {
      margin-top: 16px;
    }
  }
  &__content {
    padding-top: 144px;
    @include screen(tablet) {
      padding-top: 154px;
    }
    @include screen(laptop) {
      padding-top: 180px;
    }
  }
}