.product-card-color {
  padding: 17px 30px 30px 30px;
  border-bottom: 1px solid rgba($gray, 0.5);
  &__title {
    display: flex;
    gap: 5px;
    align-items: center;
    span {
      font-size: 16px;
      line-height: 21px;
      letter-spacing: 0.02em;
      opacity: 0.6;
      text-decoration: underline;
    }
  }
  &__items {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-top: 18px;
  }
  &__item {
    width: 43px;
    height: 43px;
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid transparent;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &.active {
      border: 1px solid $black-400;
    }
  }
  &__label {
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;
    
  }
  &__img {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    &:before {
      position: absolute;
      content: '';
      width: 41px;
      height: 41px;
      border-radius: 50%;
      top: 0;
      left: 0;
      border: 1px solid transparent;
      transition: border-color $transition-default;
    }
  }
  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;
    &:checked ~ .product-card-color__img {
      &:before {
        border: 1px solid $black-400;
        transition: border $transition-default;
      }
    }
  }
  
}