.header {
  width: 100%;
  background-color: $white;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2300;
  &.active {
    color: $white;
    background-color: transparent;
    @include screen(laptop) {
      transition: color $transition-default, background-color $transition-default;
      &:hover {
        background-color: $white;
        color: $black-400;
      }
    }
    .header__logo {
      display: none;
    }
    .header__logo-white {
      display: block;
    }
  }
  &__wrapper {
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include screen(tablet) {
      padding: 30px 0;
    }
    @include screen(laptop) {
      padding: 10.5px 0;
    }
  }
  &__logo {
    width: 77px;
    height: 20px;
    @include screen(tablet) {
      width: 96px;
      height: 26px;
    }
    @include screen(laptop) {
      width: 125px;
      height: 34px;
    }
    &-white {
      display: none;
    }
  }
  &__links {
    display: none;
    @include screen(laptop) {
      display: flex;
      gap: 30px;
    }
  }
  &__link {
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    white-space: nowrap;
    padding: 27.5px 0;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    &:hover {
      & > span,
      & a {
        cursor: pointer;
        color: $black-hover;
        border-color: $black-hover;
      }
      .header-desktop {
        opacity: 1;
        visibility: visible;
        z-index: 1;
      }
    }
    & > span,
    & > a {
      display: inline-block;
      border-bottom: 1px solid transparent;
      transition: border-color $transition-default, color $transition-default;
    }
  }
  &__search {
    width: 20px;
    height: 20px;
    cursor: pointer;
  }
  &__menu {
    width: 30px;
    height: 7px;
    cursor: pointer;
    position: relative;
    &:before,
    &:after {
      position: absolute;
      content: '';
      width: 100%;
      height: 1px;
      left: 0;
      right: 0;
      background-color: $black-400;
    }
    &:before {
      top: 0;
    }
    &:after {
      bottom: 0;
    }
    @include screen(laptop) {
      display: none;
    }
  }
  &__form {
    position: relative;
    z-index: 2;
  }
  &__label {
    position: relative;
    display: flex;
    img {
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
    }
  }
  &__input {
    padding: 15px 50px 15px 20px;
    font-size: 16px;
    width: 0;
    line-height: 21px;
    background-color: $white;
    border: 1px solid transparent;
    &.active {
      border: 1px solid rgba($gray, 0.5);
      width: 335px;
    }
    &.focus {
      border: 1px solid $black-400;
    }
  }
}