.brand-product {
  padding-top: 60px;
  padding-bottom: 60px;
  overflow: hidden;
  @include screen(laptop) {
    padding: 100px 0;
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 60px;
    @include screen(laptop) {
      gap: 150px;
    }
  }
  &__static {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 16px;
    @include screen(tablet) {
      display: grid;
      align-items: center;
      grid-template-columns: 308px 1fr;
      gap: 60px;
    }
    @include screen(laptop) {
      grid-template-columns: 447px 1fr;
      gap: 124px;
    }
    .brand-product__content {
      @include screen(tablet) {
        grid-column: 2;
        grid-row: 1;
      }
    }
    .brand-product__banner {
      @include screen(tablet) {
        grid-column: 1;
        grid-row: 1;
      }
    }
  }
  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 16px;
    @include screen(tablet) {
      display: grid;
      grid-template-columns: 1fr 308px;
      gap: 60px;
    }
    @include screen(laptop) {
      grid-template-columns: 1fr 836px;
      gap: 124px;
    }
  }
  &__banner {
    height: 494px;
    width: 100%;
    border: 1px solid $gray-400;
    padding: 38px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    @include screen(tablet) {
      grid-column: 2;
      grid-row: 1;
    }
    @include screen(laptop) {
      height: 640px;
      padding: 34px 0;
    }
    img {
      object-fit: contain;
      width: auto;
      height: 100%;
    }
  }
  &__content {
    position: relative;
    @include screen(tablet) {
      grid-column: 1;
      grid-row: 1;
    }
  }
  &__img {
    border: 1px solid $gray-400;
    width: 100%;
    height: 100%;
    background-color: #fff;
    transition: $transition-default;
    img {
      object-fit: contain;
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    font-family: 'Tenor Sans';
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    max-width: 167px;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
      max-width: 213px;

    }
  }
  &__amount {
    margin-top: 10px;
    font-size: 12px;
    line-height: 15px;
    opacity: 0.6;
  }
  &__description {
    margin-top: 16px;
    font-size: 14px;
    line-height: 18px;
    opacity: 0.8;
    @include screen(laptop) {
      max-width: 460px;
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__list {
    margin-top: 16px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    @include screen(laptop) {
      margin-top: 28px;
    }
    p {
      font-size: 14px;
      line-height: 18px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
  &__buttons {
    margin-top: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include screen(tablet) {
      justify-content: flex-start;
      gap: 10px;
    }
  }
  &__navigation {
    position: absolute;
    top: calc(494px / 2);
    width: 100%;
    z-index: 2;
    transform: translateY(-50%);
    @include screen(tablet) {
      transform: translateY(0);
      margin-top: 90px;
      width: auto;
      top: auto;
      position: static;
      display: flex;
    }
    @include screen(laptop) {
      margin-top: 170px;
    }
    .swiper-pagination {
      display: none;
      @include screen(tablet) {
        display: block;
      }
      &-bullet {
        opacity: 1;
        background-color: $gray-900;
        margin: 0 10px;
        &-active {
          background-color: $black-400;
        }
      }
    }
  }

  &__arrows {
    position: absolute;
    top: -530px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    @include screen(tablet) {
      position: relative;
      top: auto;
    }
    .swiper-button-prev,
    .swiper-button-next {
      position: relative;
    }
    @include screen(tablet) {
      justify-content: flex-start;
      display: inline-flex;
      width: auto;
      .swiper-button-prev,
      .swiper-button-next {
        top: 0;
      }
      .swiper-button-prev {
        left: 0;
      }
      .swiper-button-next {
        left: 10px;
      }
    }
  }
  &__slider {
    max-height: 494px;
    @include screen(tablet) {
      grid-column: 2;
      grid-row: 1;
      margin-top: 30px;
    }
    @include screen(laptop) {
      margin-top: 230px;
    }
    .swiper {
      &-button {
        &-prev {
          left: 15px;
        }
        &-next {
          right: 15px;
        }
      }
    }
  }
  .swiper {
    @include screen(laptop) {
      overflow: visible;
    }
    &-wrapper {
      @include screen(laptop) {
        display: flex;
        align-items: center;
      }
    }
    &-slide {
      transition: $transition-default;
      display: flex;
      align-items: center;
      justify-content: center;

      height: 494px;
      padding: 38px 0;
      @include screen(laptop) {
        opacity: 0;
        height: 295px;
        .brand-product__img {
          height: 295px;
          padding: 26px 0;
          img {
            width: 100%;
            object-fit: contain;
            height: 100%;
          }
        }
      }
      &-active {
        @include screen(laptop) {
          z-index: 2;
          opacity: 1;
        }
        & ~ .swiper-slide {
          @include screen(laptop) {
            opacity: 1;
          }
        }
        .brand-product__img {
          @include screen(laptop) {
            transform: scale(1.4) translateX(50px);
            max-width: 200px;
            height: 450px;
            padding: 16px 0;
          }
        }
      }
    }
    &-button {
      &-prev,
      &-next {
        display: flex;
        margin: 0;
        width: 45px;
        height: 45px;
        border-radius: 50%;
        border: 1px solid $black-400;
        background-color: transparent;
        align-items: center;
        justify-content: center;
        svg {
          width: 19px;
        }
        &:after {
          display: none;
        }
      }
      &-next {
        svg {
          transform: rotate(180deg);
        }
      }
    }
  }
  .swiper-pagination-bullets.swiper-pagination-horizontal {
    width: auto;
    left: auto;
    right: 50px;
  }
  .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 10px;
  } 
  &-popup {
    position: absolute;
    top: 82px;
    width: 520px;
    height: auto;
    border: 1px solid $black-400;
    background-color: $white;

    visibility: hidden;
    z-index: -1;
    opacity: 0;
    transition: visibility $transition-default, z-index $transition-default, opacity $transition-default;
    &.active {
      visibility: visible;
      z-index: 10;
      opacity: 1;
    }
    &__wrapper {
      padding: 20px 25px;
    }
    &__title {
      font-family: 'Tenor Sans';
      font-size: 16px;
      line-height: 21px;
      letter-spacing: 0.02em;
    }
    &__description {
      margin-top: 26px;
      font-size: 16px;
      line-height: 21px;
      opacity: 0.8;
      max-height: 181px;
      overflow-y: auto;
    }
  }
}