.brand-hero {
  display: block;
  position: relative;
  height: calc(100vh - 148px);
  @include screen(tablet) {
    height: calc(100vh - 170px);
  }
  @include screen(laptop) {
    height: calc(100vh - 161px);
  }
  &__cube {
    display: block;
    position: relative;
    top: auto;
    left: auto;
    z-index: 5;
    @include screen(laptop) { 
      margin-right: 0;
      margin-left: auto;
      cursor: none;
      right: 13%;
      margin-top: 0;
      min-width: 544px;
      min-height: 544px;
      max-width: 544px;
      max-height: 544px;
      top: 50%;
      transform: translateY(calc(-50% - 80px));
    }
    .swiper {
      max-width: 170px;
      max-height: 158px;
      min-height: 158px;
      min-width: 170px;
      margin: 0;
      @include screen(tablet) {
        min-width: 230px;
        min-height: 230px;
        max-width: 230px;
        max-height: 230px;
      }
      @include screen(laptop) {
        min-width: 544px;
        min-height: 544px;
        max-width: 544px;
        max-height: 544px;
      }
    }
    picture {
      display: block;
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        aspect-ratio: 1 / 1;
        object-fit: cover;
        object-position: center;
      }
    }
  }
  &__wrapper {
    z-index: 4;
    position: relative;
    height: 100%;
    width: 100%;
    .wrapper {
      height: 100%;
    }
  }
  &__bg {
    display: inline-block;
    position: absolute;
    top: -99px;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 3;
    @include screen(tablet) {
      top: -175px;
    }
    @include screen(laptop) {
      top: -144px;
      cursor: none;
    }
    .swiper {
      height: 100%;
    }
    picture {
      height: 100%;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.2);
        z-index: 2;
      }
    }
    img {
      height: 100%;
      object-fit: cover;
      width: 100%;
    }
  }
  &__inner {
    height: 100%;
    position: relative;
  }
  &__content {
    color: $white;
    position: relative;
    max-width: 335px;
    margin-top: 320px;
    @include screen(tablet) {
      margin-top: 448px;
    }
    @include screen(laptop) {
      margin-top: 0;
      top: 50%;
      transform: translateY(-50%);

    }
  }
  &__name {
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 10px;
  }
  &__text {
    margin-top: 16px;
    max-width: 300px;
    font-size: 14px;
    line-height: 18px;
    @include screen(laptop) {
      margin-top: 24px;
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__cursor {
    position: fixed;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.3s ease;
    width: 172px;
    height: 172px;
    border-radius: 50%;
    border: 1px solid $black-400;
    background-color: rgba($white, 0.5);
    display: none;
    z-index: 100;
    transform: translate(-50%, -50%);
    top: 0;
    left: 0;
    align-items: center;
    justify-content: center;
    @include screen(laptop) {
      display: flex;
    }
    span {
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__button {
    margin-top: 32px;
    padding: 16px 47px;
    border: 1px solid $white;
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    cursor: pointer;
    span {
      font-size: 14px;
      line-height: 18px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
    @include screen(laptop) {
      display: none;
    }
  }
  &__list {
    margin-top: 24px;
    flex-direction: column;
    gap: 12px;
    display: none;
    @include screen(laptop) {
      margin-top: 78px;
      display: flex;

    }
  }
  &__li {
    display: flex;
    align-items: center;
    gap: 8px;
    span {
      font-size: 14px;
      line-height: 18px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
  &-numbers {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-pagination {
    position: absolute;
    right: 0;
    color: $white;
    bottom: 37px;
    z-index: 3;
    width: 100%;
    .swiper-pagination {
      position: relative;
      display: flex;
      justify-content: flex-end;
      top: auto;
      right: auto;
      bottom: auto;
      left: auto;
      margin: 0;
      span {
        display: inline-block;
        margin: 0 4px;
      }
    }
  }
}
.brand-hero2 {
  .brand-hero__bg {
    cursor: default;
  }
}