* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

textarea,
input,
button,
select,
a {
  outline: none;
  border-radius: 0;

  font-weight: 400;
  font-family: 'Jost';
  touch-action: manipulation;
}

html,
body {
  height: 100%;
  font-family: 'Jost';
  color: $black-400;
}

h2 {
  font-family: 'Tenor Sans';
  font-size: 20px;
  line-height: 26px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  @include screen(tablet) {
    font-size: 24px;
    line-height: 32px;
  }
  @include screen(laptop) {
    font-size: 28px;
    line-height: 36px;
  }
}

.cover {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  overflow: hidden;
  background-color: $black-400;
  opacity: 0;
  z-index: -1;
  visibility: hidden;
  @include screen(laptop) {
    transition: opacity $transition-default;
  }
  &.active {
    opacity: 0.05;
    z-index: 2499;
    visibility: visible;
    transition: opacity $transition-default;
  }
}

main {
  padding-top: 93px;
  @include screen(tablet) {
    padding-top: 113px;
  }
  @include screen(laptop) {
    padding-top: 73px;
  }
}

.blank {
  padding: 80px 0;
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }
}

.render {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  background-color: $white;
  &:before {
    content: '';
    position: absolute;
    width: 48px;
    height: 48px;
    border: 2px solid $black-400;
  }
  &:after {
    content: '';
    position: absolute;
    width: 48px;
    z-index: 2;
    height: 48px;
    border: 1px solid $gray;
    animation: rotate-preloader 2s linear infinite;
  }
  @keyframes rotate-preloader {
    100% { 
      transform: rotate(360deg); 
    } 
  }
}

.wrapper {
  margin: 0 auto;
  padding: 0 #{$container-padding};
  max-width: calc(#{$mobile-wrapper} + #{$container-padding * 2});
  @include screen(tablet) {
    max-width: calc(#{$screen-tablet} + #{$container-padding * 2});
  }
  @include screen(tablet-2x) {
    max-width: calc(#{$screen-tablet-2x} + #{$container-padding * 2});
  }
  @include screen(laptop) {
    max-width: calc(#{$screen-laptop} + #{$container-padding * 2});
  }
  @include screen(desktop) {
    max-width: calc(#{$screen-desktop} + #{$container-padding * 2});
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	-webkit-appearance:none;
}