.catalog-filter {
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title {
    position: relative;
    span {
      position: absolute;
      bottom: 90%;
      left: 100%;
      font-size: 10px;
      line-height: 13px;
      letter-spacing: 0.02em;
    }
  }
  &__items {
    margin-top: 24px;
    display: grid;
    grid-template-columns:  repeat(2, minmax(0, 1fr));
    gap: 10px;
    @include screen(tablet) {
      max-width: 670px;
      grid-template-columns:  repeat(4, minmax(0, 1fr));
    }
    @include screen(laptop) {
      display: flex;
      max-width: max-content;
      flex-wrap: wrap;
    }
  }
  &__item {
    .button {
      width: 160px;
    }
    &.active {
      .button {
        background-color: $black-400;
        span {
          color: $white;
        }
      }
    }
  }
}