.catalog-cards {
  padding-top: 40px;
  padding-bottom: 60px;
  @include screen(tablet) {
    padding-top: 32px;
  }
  @include screen(laptop) {
    padding-top: 50px;
    padding-bottom: 100px;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 10px;
    @include screen(tablet) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
    @include screen(laptop) {
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
  }
  .card-new {
    &__center {
      display: none;
    }
  }
  &__banner {
    padding: 15px;
    background-image: linear-gradient(90deg, #FCFAFB 0%, #FAF8F9 15%, #F7F6F4 31.3%, #F6F5F3 45.65%, #F5F3F4 62.45%, #F6F4F5 79.09%, #F8F7F5 88.99%, #F8F7F5 100%);
    border: 1px solid $gray-400;
    position: relative;
    grid-column: 2;
	  grid-row: 1;
    @include screen(tablet) {
      grid-column: 3;
      padding: 30px 20px;
    }
    @include screen(laptop) {
      grid-column: 3 / span 2;
      padding: 20px 30px;

    }
  }
  &__img {
    position: absolute;
    height: 250px;
    width: 100%;
    left: 0;
    bottom: 0;
    right: 0;
    object-position: 40px 10px;
    @include screen(tablet) {
      height: 270px;
    }
    @include screen(tablet) {
      height: 100%;
    }
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      object-position: 37% 100%;
      @include screen(tablet) {
        object-position: 0% 100%;
      }
      @include screen(tablet) {
        object-position: 50% 50%;
      }
    }
  }
  &__content {
    position: relative;
    z-index: 2;
  }
  &__button {
    margin-top: 10px;
    @include screen(tablet) {
      margin-top: 16px;
    }
    @include screen(laptop) {
      margin-top: 24px;
    }
    .button {
      width: 120px;
      @include screen(tablet) {
        width: 150px;
      }
    }
  }
  &__name {
    font-family: 'Tenor Sans';
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    @include screen(tablet) {
      font-size: 16px;
      line-height: 21px;
    }
    @include screen(laptop) {
      font-size: 24px;
      line-height: 32px;
      max-width: 238px;
    }
  }
}